@import url('https://fonts.googleapis.com/css2?family=SF+Pro+Text:wght@400;700&display=swap');

:root {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  color: #ffffff;
  text-align: center;
  position: relative;
  background-color: #000000;
  overflow: hidden;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("https://e1.pxfuel.com/desktop-wallpaper/159/964/desktop-wallpaper-macos-big-sur-apple-layers-fluidic-colorful-wwdc-stock-2020-gradients.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(5px);
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 50px;
  background-color: #ffffff7d;
  border-radius: 50px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(33px);
  background-blend-mode: overlay;
}

.element {
  margin-bottom: 30px;
}

h1 {
  font-family: 'SF Pro Text', 'Arial', sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  background-color: #ffffff7d;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 10px;
}

.strength-meter {
  position: relative;
  height: 2rem;
  width: 70%;
  background-color: #ffffff7d;
  border-radius: 1rem;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  overflow: hidden;
}

.strength-meter::before {
  content: '';
  position: absolute;
  left: 0;
  height: 100%;
  width: calc(var(--strength, 0) * 1%);
  background-color: #007aff;
  transition: width 500ms ease-in-out;
  z-index: 1;
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 5px,
    rgba(255, 255, 255, 0.1) 5px,
    rgba(255, 255, 255, 0.1) 10px
  );
}

.password-input {
  margin-top: 2rem;
  width: 45%;
  padding: 0.55rem;
  color: #80868a;
  font-family: 'SF Pro Text', 'Arial', sans-serif;
  font-size: 1rem;
  background-color: #ffffff7d;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #4e5c6b;
  outline: none;
  text-align: center;
}

.password-input:focus {
  border-color: #687580;
}

.reasons {
  font-family: 'SF Pro Text', 'Arial', sans-serif;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.8);
  background-color: #ffffff7d;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 10px;
}

.reasons > * {
  margin-top: 0.10rem;
  color: #80868a;
}
